import echarts from 'echarts'



export default{

  data: () => ({

    calendarStart: '',

    calendarMonth: new Date().toLocaleString('en-us', { month: 'long' }),

    calendarYear: new Date().getFullYear(),

    waterCollectionRate: 0, //gpm

    waterConsumptionRate: 5, //gpm

    elecConsumptionRate: 90, //kW

    imageHeight: 100,

    LBCImage1: null,

    loading: false,

    search: null,

    select: null,

    waterChart: null,

    sunChart: null,

    weatherChart: null,

    weatherChartOption: null,

    powerSankey: null,

    waterLevelPerc: 50,

    solarPower: 8,

    starPath: 'path://m 77.107143,118.59523 0.75595,-32.883922 18.520837,26.080362 24.56845,-23.812504 -4.53572,32.505944 31.08854,-3.21279 -24.47395,20.97767 24.75744,24.94643 -34.01786,-4.15774 0.37798,34.39584 -21.166668,-27.21429 -24.568454,22.3006 5.669644,-31.37202 -30.238095,2.64583 23.434524,-21.16667 -22.67857,-24.19047 z',

    solarTrajectoryOption: 1,

    solarTrajectory: [[{coords: [[3,10],[4,8.2]]},{coords: [[4,10],[5,8.2]]},{coords: [[5,10],[6,8.2]]},{coords: [[6,10],[7,8.2]]},{coords: [[7,10],[8,8.2]]},{coords: [[8,10],[9,8.2]]},{coords: [[9,10],[10,8.2]]}],

                      [{coords: [[5,10],[4,8.2]]},{coords: [[5,10],[5,8.2]]},{coords: [[5,10],[6,8.2]]},{coords: [[5,10],[7,8.2]]},{coords: [[5,10],[8,8.2]]},{coords: [[5,10],[9,8.2]]},{coords: [[5,10],[10,8.2]]}]],

    weatherOption: 0,

    weatherColors: ['background-color:rgba(255,185,0,0.9)','background-color:rgba(20,20,155,1)','background-color:rgba(20,20,155,1)'],

    rainTrajectory1a: [],

    rainTrajectory1b: [],

    rainTrajectory2a: [],

    rainTrajectory2b: [],

    rainTrajectory3a: [],

    rainTrajectory3b: [],

    calendarEvents: [

        {

          title: 'Company Event',

          details: 'Recruitment event for internships',

          date: '2019-03-30',

          people: '50',

          open: false

        },

        {

          title: 'Student Club Fundraiser',

          details: 'Student hosted event to raise money for club',

          date: '2019-03-31',

          people: '80',

          open: false

        },

        {

          title: 'Award Ceremony',

          details: 'Department teaching and research award reception',

          date: '2019-04-01',

          people: '100',

          open: false

        },

        {

          title: 'Georgia Tech Foundation Gala',

          details: 'Fundraising event for the GT Foundation',

          date: '2019-03-07',

          people: '150',

          open: false

        },

        {

          title: 'Research Meetings with Sponsors',

