          date: '2019-03-03',

          people: '25',

          open: false

        },

        {

          title: 'Graduate Student Appreciation Event',

          details: 'Reception for grad students and their advisors',

          date: '2019-03-01',

          people: '75',

          open: false

        },

        {

          title: 'Conference',

          details: 'Small conference for undergraduate research',

          date: '2019-03-21',

          people: '250',

          open: false

        },

        {

          title: 'Hackathon',

          details: 'Code like there is no tommorrow',

          date: '2019-03-01',

          people: '100',

          open: false

        }

      ],

  }),

  methods: {

    navigate(item,index){

      console.log('navigating...')

      console.log(router.currentRoute)

      router.push({name: item});

    },

    getCalendarYear(){

      let dateObject = new Date();

      if(this.calendarStart !== ''){

        dateObject = new Date(this.calendarStart.replace("-","/"));

      }

      this.calendarYear = dateObject.getFullYear();

    },

    getCalendarMonth(){

      let dateObject = new Date();

      if(this.calendarStart !== ''){

        dateObject = new Date(this.calendarStart.replace("-","/"));

      }

      this.calendarMonth = dateObject.toLocaleString('en-us',{ month: 'long' });

    },

    refreshWaterChart(){

      const waterChartOption = {

        color:'blue',

        grid: {

          bottom: 0,

          left: 0,

          top: 0,

          right: 0,

        },

        xAxis: {

          data: ['Water'],

          show: false,

        },

        yAxis: {

          show: false,

          min: 0,

          max: 100,

        },

        series: [

          {

            type: 'bar',

            data: [this.waterLevelPerc],

          },

        ],

      };

      this.waterChart.setOption(waterChartOption,true)

    },

    refreshSunChart(){

      const sunChartOption = {

        grid: {

          top: 5,

        },

        xAxis: {

            type: 'value',

            show: false,

            min: 0,

            max: 10,

        },

        yAxis: {

            min: 0,

            max: 10,

            show: false,

        },

        series: [

        {

            name: 'tail',

            type: 'lines',

            coordinateSystem: 'cartesian2d',

            zlevel: 1,

            effect: {

                show: true,

                period: 10-this.solarPower,

                trailLength: .8,

                color: '#ffa022',

                symbolSize: 3

            },

            lineStyle: {

                normal: {

                    color: '#ffa022',

                    width: 0,

                    curveness: 0.05

                }

            },

            data: this.solarTrajectory[this.solarTrajectoryOption-1]

        },

        {

            name: 'sun',

            type: 'lines',

            coordinateSystem: 'cartesian2d',

            zlevel: 2,

            //symbol: ['none', 'path://m 27.553357,212.43475 77.379793,-63.40918 -17.160941,-4.09821 90.815721,-72.69271 -73.03569,68.18926 16.94412,4.33925 z'],

            symbolSize: 25,

            effect: {

                show: true,

                period: 10-this.solarPower,

                trailLength: 0,

                symbol: this.starPath,

                symbolSize: 15

            },

            lineStyle: {

                normal: {

                    color: '#ffa022',

                    width: 0,

                    opacity: 0.6,

                    curveness: 0.05

                }

            },

            data: this.solarTrajectory[this.solarTrajectoryOption-1]

        },

        ],

      };

      this.sunChart.setOption(sunChartOption,true);

    },

    getRainData(){

      let rainData = [];

      for(let i=0; i<30;i++){

        let x = Math.random()*1.3;

        rainData.push({coords: [[x,10],[x+0.2,0]]})

      };

      this.rainTrajectory1a = rainData.splice(0,rainData.length/2);

      this.rainTrajectory1b = rainData.splice(rainData.length/2,rainData.length);



      rainData = [];

      for(let i=0; i<50;i++){

        let x = Math.random()*2.5+1.3

        rainData.push({coords: [[x,10],[x+0.2,5]]})

      };

      this.rainTrajectory2a = rainData.splice(0,rainData.length/2);

      this.rainTrajectory2b = rainData.splice(rainData.length/2,rainData.length);



      rainData = [];

      for(let i=0; i<90;i++){

        let x = Math.random()*6.5+3.8

        rainData.push({coords: [[x,10],[x+0.2,8.2]]})

      };

      this.rainTrajectory3a = rainData.splice(0,rainData.length/2);

      this.rainTrajectory3b = rainData.splice(rainData.length/2,rainData.length);

    },

    refreshWeatherChart(){

      if(this.weatherOption == 0){

        this.waterCollectionRate = 0; //gpm

        this.solarPower = 8;

        if(this.weatherChartOption !== null){

          this.weatherChartOption.series = [];

          this.weatherChart.setOption(this.weatherChartOption,false)

        }

      }

      else if(this.weatherOption == 1){

        this.waterCollectionRate = 0; //gpm

        this.solarPower = 4;

        if(this.weatherChartOption !== null){

            this.weatherChartOption.series = [];

            this.weatherChart.setOption(this.weatherChartOption,false)

        }

      }else if(this.weatherOption==2){

        this.waterCollectionRate = 10; //gpm

        this.solarPower = 1;

        this.weatherChartOption = {

          grid: {

            top: 0,

            left: 0,

          },

          xAxis: {

              type: 'value',

              show: false,

              min: 0,

              max: 10,

          },

          yAxis: {

              min: 0,

              max: 10,

              show: false,

          },

          series: [

          {

              name: 'tail', type: 'lines', coordinateSystem: 'cartesian2d', zlevel: 1,

              effect: {

                  show: true, constantSpeed: 40, trailLength: .3, color: 'blue', symbolSize: 1

              },

              lineStyle: {

                  normal: {

                      color: 'blue', width: 0, curveness: 0

                  }

              },

              data: this.rainTrajectory1a

          },

          {

              name: 'tail', type: 'lines', coordinateSystem: 'cartesian2d', zlevel: 1,

              effect: {

                  show: true, constantSpeed: 60, trailLength: .3, color: 'blue', symbolSize: 1

              },

              lineStyle: {

                  normal: {

                      color: 'blue', width: 0, curveness: 0

                  }

              },

              data: this.rainTrajectory1b

          },

          {

              name: 'tail', type: 'lines', coordinateSystem: 'cartesian2d', zlevel: 1,

              effect: {

                  show: true, constantSpeed: 40, trailLength: .3, color: 'blue', symbolSize: 1

              },

              lineStyle: {

                  normal: {

                      color: 'blue', width: 0, curveness: 0

                  }

              },

              data: this.rainTrajectory2a

          },

          {

              name: 'tail', type: 'lines', coordinateSystem: 'cartesian2d', zlevel: 1,

              effect: {

                  show: true, constantSpeed: 60, trailLength: .3, color: 'blue', symbolSize: 1

              },

              lineStyle: {

                  normal: {

                      color: 'blue', width: 0, curveness: 0

                  }

              },

              data: this.rainTrajectory2b

          },

          {

              name: 'tail', type: 'lines', coordinateSystem: 'cartesian2d', zlevel: 1,

              effect: {

                  show: true, constantSpeed: 40, trailLength: .3, color: 'blue', symbolSize: 1

              },

              lineStyle: {

                  normal: {

                      color: 'blue', width: 0, curveness: 0

                  }

              },

              data: this.rainTrajectory3a

          },

          {

              name: 'tail', type: 'lines', coordinateSystem: 'cartesian2d', zlevel: 1,

              effect: {

                  show: true, constantSpeed: 60, trailLength: .3, color: 'blue', symbolSize: 1

              },

              lineStyle: {

                  normal: {

                      color: 'blue', width: 0, curveness: 0

                  }

              },

              data: this.rainTrajectory3b

          },

          ],

        };

        this.weatherChart.setOption(this.weatherChartOption,false)

      }

      this.refreshSunChart();

    },

    refreshPowerSankey(){

      const powerSankeyOption = {

        title: {

          text: 'Current Power Flow (kW)',

          textAlign: 'auto'

        },

        tooltip: {

          trigger: 'item',

          triggerOn: 'mousemove'

        },

        series: [

        {

            type: 'sankey',

            layout: 'none',

            // effect: {

            //     show: true,

            //     period: 10-this.solarPower,

            //     trailLength: .8,

            //     color: '#ffa022',

            //     symbolSize: 3

            // },

            focusNodeAdjacency: 'allEdges',

            lineStyle: {

                normal: {

                    color: 'source',

                    curveness: 0.05

                }

            },

            data: [

              {name: 'Solar Panels', value: this.solarPower*12},

              {name: 'Campus Grid', value: this.solarPower*12-this.elecConsumptionRate},

              {name: 'Kendeda', value: this.elecConsumptionRate},

              {name: 'HVAC', value: this.elecConsumptionRate/2},

              {name: 'Plug', value: this.elecConsumptionRate/4},

              {name: 'Lighting', value: this.elecConsumptionRate/4},

            ],

            links: [

              //{source: 'Campus Grid', target: 'Kendeda', value: this.elecConsumptionRate-this.solarPower*12},

              {source: 'Solar Panels', target: 'Kendeda', value: this.solarPower*12},

              {source: 'Kendeda', target: 'HVAC', value: this.elecConsumptionRate/2},

              {source: 'Kendeda', target: 'Plug', value: this.elecConsumptionRate/4},

              {source: 'Kendeda', target: 'Lighting', value: this.elecConsumptionRate/4},

              {source: 'Solar Panels', target: 'Campus Grid', value: this.solarPower*12-this.elecConsumptionRate},

            ],

        },

        ],

      };

      this.powerSankey.setOption(powerSankeyOption,true);

    },

    open (event) {

        alert(event.title)

    },

  },

  created() {

    // this.LBCImage1 = new Image();

    // this.LBCImage1.src = 'D:/Libraries/Documents/VueCoding/CNC/src/assets/LBC1.png';

    // document.body.appendChild(this.LBCImage1);

  },

