    calendarEventsMap () {

        const map = {}

        this.calendarEvents.forEach(e => (map[e.date] = map[e.date] || []).push(e))

        return map

    },

  },

  mounted: function () {

    this.$nextTick(function () {

      // Code that will run only after the

      // entire view has been rendered



      //this.imageHeight = this.$refs.imageCard.clientHeight;

      this.waterChart = echarts.init(this.$refs.waterEcharts);

      window.addEventListener('resize', this.waterChart.resize);

      this.refreshWaterChart();

      this.sunChart = echarts.init(this.$refs.sunEcharts);

      window.addEventListener('resize', this.sunChart.resize);

      this.refreshSunChart();

      this.weatherChart = echarts.init(this.$refs.weatherEcharts);

      window.addEventListener('resize', this.weatherChart.resize);

      this.getRainData();

      this.refreshWeatherChart();

      this.powerSankey = echarts.init(this.$refs.powerSankey);

      window.addEventListener('resize', this.powerSankey.resize);

      this.refreshPowerSankey();

    })

